<template>
  <div>
    <el-row style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-button @click="clearFilter"></el-button>
        </el-form-item>
        <div style="float:right">
          <el-form-item>
            <el-input v-model="filters.query" placeholder="/"/>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-search"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-plus"></el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-row>
    <el-table
      ref="filterTable"
      :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
      :default-sort="{prop: 'date', order: 'descending'}"
      highlight-current-row
    >
      <!-- typeselection Checkbox -->
      <el-table-column type="selection" width="55"></el-table-column>
      <!-- filtersfilter-method -->
      <el-table-column
        prop="status"
        label=""
        width="100"
        :filters="statusDropdown"
        :filter-method="filterHandler"
      >
        <template slot-scope="scope">
          <el-dropdown trigger="click">
            <el-tag>
              {{scope.row.status}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-tag>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                v-for="status in statusDropdown"
                :key="status.value"
                @click.native="statusDropdownChoose(scope,status.text)"
              >{{status.text}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" width="100">
        <template slot-scope="scope">
          <div>
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        </template>
      </el-table-column>
      <!-- formatter ,rowcolumn-->
      <el-table-column prop="address" label="" :formatter="formatter"></el-table-column>
      <el-table-column
        prop="priority"
        label=""
        :filters="priorityDropdown"
        :filter-method="filterHandler"
      >
        <template slot-scope="scope">
          <el-dropdown trigger="click">
            <el-tag>
              {{scope.row.priority}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-tag>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                v-for="priorities in priorityDropdown"
                :key="priorities.value"
                @click.native="priorityDropdownChoose(scope,priorities.text)"
              >{{priorities.text}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
      <el-table-column prop="belongto" label=""></el-table-column>
      <el-table-column prop="date" label="" width="140" sortable>
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.date }}</span>
        </template>
      </el-table-column>
      <el-table-column align="center">
        <template slot="header">
          <el-input size="mini" v-model="search" placeholder=""/>
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form" :label-position="labelPosition">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.belongto" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.date" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.status" disabled>
            <el-option v-for="status in statusDropdown" :key="status.text" :value="status.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.priority" disabled>
            <el-option
              v-for="priorities in priorityDropdown"
              :key="priorities.text"
              :value="priorities.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Mock from "mockjs";

//mock
var Random = Mock.Random;
Random.extend({
  status: function(date) {
    var statusDropdown = ["","","","",""];
    return this.pick(statusDropdown);
  },
  priority: function(date) {
    var priorityDropdown = ["","","",""];
    return this.pick(priorityDropdown);
  }
});
let tableData = [];
for (let i = 0; i < 60; i++) {
  tableData.push(
    Mock.mock({
      date:Random.datetime('yyyy-MM-dd'),
      name: "@cname",
      address: '@province@city',
      status: "@status",
      priority: "@priority",
      belongto: "@name"
    })
  );
}
let statusDropdown = [
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" }
];
let priorityDropdown = [
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" }
];

let data = () => {
  return {
    search: "",
    //
    filters: {},
    //
    statusDropdown,
    //
    priorityDropdown,
    tableData,
    dialogFormVisible: false,
    labelPosition: "left",
    form: {
      name: "",
      status: "",
      priority: "",
      date: "",
      belongto: "",
      address: ""
    },
    formLabelWidth: "80px"
  };
};

export default {
  data: data,
  methods: {
    formatter: function(row, column) {
      return row.address;
    },
    //
    clearFilter() {
      this.$refs.filterTable.clearFilter();
    },
    //
    statusDropdownChoose: function(scope, text) {
      scope.row.status = text;
      //
    },
    priorityDropdownChoose: function(scope, text) {
      scope.row.priority = text;
    },
    filterHandler: function(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    handleEdit: function(index, row) {
      this.dialogFormVisible = true;
      // this.form = Object.assign({}, row);
      //rowform
      this.form = row;
    }
  },
  mounted: function() {}
};
</script>



