<template>
  <div>
    <el-form :inline="true" :model="filters">
      <el-form-item>
        <el-button class="btn-default" @click="clearFilter"></el-button>
        <el-button class="btn-default">
          <i class="el-icon-refresh"></i>
          <span></span>
        </el-button>
      </el-form-item>
      <div style="float:right">
        <el-form-item>
          <el-input v-model="search" placeholder=""/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="buttonQuery"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-plus" @click="buttonAdd"></el-button>
        </el-form-item>
      </div>
    </el-form>
    <el-table
      ref="filterTable"
      :data="tables | pagination(pageNo,pageSize)"
      :default-sort="{prop: 'date', order: 'descending'}"
      highlight-current-row
    >
      <!-- typeselection Checkbox -->
      <el-table-column type="selection" width="55"></el-table-column>
      <!-- filtersfilter-method -->
      <el-table-column
        prop="status"
        label=""
        width="100"
        :filters="statusDropdown"
        :filter-method="filterHandler"
      >
        <template slot-scope="scope">
          <el-dropdown trigger="click">
            <el-tag>
              {{scope.row.status}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-tag>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                v-for="status in statusDropdown"
                :key="status.value"
                @click.native="statusDropdownChoose(scope,status.text)"
              >{{status.text}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" width="100">
        <template slot-scope="scope">
          <div>
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        </template>
      </el-table-column>
      <!-- formatter ,rowcolumn-->
      <el-table-column prop="brief" label="" :formatter="formatter"></el-table-column>
      <el-table-column
        prop="priority"
        label=""
        :filters="priorityDropdown"
        :filter-method="filterHandler"
      >
        <template slot-scope="scope">
          <el-dropdown trigger="click">
            <el-tag>
              {{scope.row.priority}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-tag>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                v-for="priorities in priorityDropdown"
                :key="priorities.value"
                @click.native="priorityDropdownChoose(scope,priorities.text)"
              >{{priorities.text}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
      <el-table-column prop="belongto" label=""></el-table-column>
      <el-table-column prop="date" label="" width="140" sortable>
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.date }}</span>
        </template>
      </el-table-column>
      <el-table-column align="center">
        <template slot="header">
          <el-input size="mini" v-model="search" placeholder=""/>
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click.native.prevent="handleDelete(scope.$index, tableData)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      style="float:right;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNo"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length"
    ></el-pagination>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form" :label-position="labelPosition">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.brief" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.belongto" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.date" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.status" :disabled="isDisable">
            <el-option v-for="status in statusDropdown" :key="status.text" :value="status.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.priority" :disabled="isDisable">
            <el-option
              v-for="priorities in priorityDropdown"
              :key="priorities.text"
              :value="priorities.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
//mock
require("../../mock/table");

let tableData = [];
let statusDropdown = [
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" }
];
let priorityDropdown = [
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" },
  { text: "", value: "" }
];

let data = () => {
  return {
    search: "",
    //
    filters: {
      name: ""
    },
    //
    statusDropdown,
    //
    priorityDropdown,
    //
    tableData,
    //
    dialogFormVisible: false,
    //select
    isDisable: true,
    //
    labelPosition: "left",
    //
    form: {
      name: "",
      status: "",
      priority: "",
      date: "",
      belongto: "",
      brief: ""
    },
    formLabelWidth: "80px",
    pageNo: 1,
    pageSize: 10
  };
};
export default {
  data: data,
  filters: {
    //
    pagination(data, pageNo, pageSize) {
      let offset = (pageNo - 1) * pageSize;
      let result =
        offset + pageSize >= data.length
          ? data.slice(offset, data.length)
          : data.slice(offset, offset + pageSize);
      return result;
    }
  },
  methods: {
    formatter: function(row, column) {
      return row.brief;
    },
    //
    clearFilter() {
      //reffiltertabletable
      this.$refs.filterTable.clearFilter();
    },
    //
    statusDropdownChoose: function(scope, text) {
      if (scope.row.status != text) {
        scope.row.status = text;
        this.$message({
          showClose: true,
          message: "",
          type: "success"
        });
      }
      //
    },
    priorityDropdownChoose: function(scope, text) {
      if (scope.row.priority != text) {
        scope.row.priority = text;
        this.$message({
          showClose: true,
          message: "",
          type: "success"
        });
      }
    },
    filterHandler: function(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    //
    handleEdit: function(index, row) {
      this.dialogFormVisible = true;
      this.isDisable = true;
      // this.form = Object.assign({}, row);
      //rowform
      this.form = row;
    },
    //
    handleDelete(index, row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          //
          row.splice(index, 1);
          //

          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //(10/page->20/page)
    handleSizeChange(val) {
      //pagesize
      this.pageSize = val;
      // console.log(` ${val} `);
    },
    //
    handleCurrentChange(val) {
      this.pageNo = val;
      // console.log(`: ${val}`);
    },
    buttonQuery() {},
    buttonAdd() {
      this.dialogFormVisible = true;
      this.isDisable = false;
    },

    test(data) {
      let search = this.search;
      console.log(data);
      return !search || data.name.toLowerCase().includes(search.toLowerCase());
    }
  },
  created() {},
  mounted: function() {
    //axiospost
    this.$axios.post("/Problem/All").then(res => {
      this.tableData = res.data.array;
      // console.log(res.data);
      // this.tableData=res.data;
      // this.msg = res.data.name;
      // console.log(this.msg);
    });
  },
  computed: {
    // 
    tables() {
      const search = this.search;
      if (search) {
        // filter() 
        //  filter() 
        //  filter() 
        return this.tableData.filter(data => {
          // some() ;
          // some() 
          // true , ;
          // false
          //  some() 
          //  some() 
          return Object.keys(data).some(key => {
            //keydatadatename.....
            // indexOf() -1
            // toLowerCase()
            return (
              String(data[key])
                .toLowerCase()
                .indexOf(search) > -1
            );
          });
        });
      }
      return this.tableData;
    }
  },
  watch: {
    //
    $route: function(to, from) {
      if (this.$route.path == "/Problem/All") {
        this.$axios.post("/Problem/All").then(res => {
          this.tableData = res.data.array;
        });
      }
      if (this.$route.path == "/Problem/ToSolve") {
        this.$axios.post("/Problem/ToSolve").then(res => {
          this.tableData = res.data.array;
        });
      }
      if (this.$route.path == "/Problem/Mine") {
        this.$axios.post("/Problem/Mine").then(res => {
          this.tableData = res.data.array;
        });
      }
      if (this.$route.path == "/Problem/Create") {
        this.$axios.post("/Problem/Create").then(res => {
          this.tableData = res.data.array;
        });
      }
      if (this.$route.path == "/Problem/Distr") {
        this.$axios.post("/Problem/Distr").then(res => {
          this.tableData = res.data.array;
        });
      }
      if (this.$route.path == "/Problem/Unsolve") {
        this.$axios.post("/Problem/Unsolve").then(res => {
          this.tableData = res.data.array;
        });
      }
    }
  }
};
</script>
<style scoped>
.el-form {
  background-color: #ffffff;
}
.el-form-item {
  margin-top: 10px;
  margin-bottom: 10px;
}
.btn-default {
  box-shadow: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
}
</style>



